.column {
  width: 30px;
  height: 200px;
  background: -webkit-gradient(linear, left top, left bottom, from(red), color-stop(80%, blue), color-stop(80%, yellow), to(orange));
  background: -webkit-linear-gradient(red, blue 80%, yellow 80%, orange);
  background: -o-linear-gradient(red, blue 80%, yellow 80%, orange);
  background: linear-gradient(red, blue 80%, yellow 80%, orange);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
}


